import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
/*import Maps from '@/components/Maps'
*/
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    }
   /* {
      path: '/maps',
      name: 'Maps',
      component: Maps
    }*/
  ]
})



