<template>


  <div class="EventLocation__wrapper">
    <div id="location">
      <gmap-autocomplete id="location1" @place_changed="setPlace"></gmap-autocomplete>
      <gmap-map
        :center="location"
        :zoom="6"
        style="width: 100%; height: 300px">
        <gmap-marker
          :position="location"
          :clickable="true"
          :draggable="true"
          @click="center=location"
          @place_changed="setPlace"
          @position_changed="markerDrag($event)"
        ></gmap-marker>
      </gmap-map>
    </div>

    <input type="hidden" v-model="location.lat" name="lat">
    <input type="hidden" v-model="location.lng" name="lng">  

  </div>
  
</template>


<script>
  export default {
    name: 'Maps', 
    data () {
      return {
        location: {
          lat: 44.787197,
          lng: 20.457273
        },
        markers: [{
          position: {lat: 10.0, lng: 10.0}
        }]
      }
    },

    methods: {
      setPlace (place) {
        this.location = {
          lat: place.geometry.location.lat(),
          lng: place.geometry.location.lng()
        }
      },

      markerDrag (position) {
        this.location = {
          lat: position.lat(),
          lng: position.lng()
        }
      }
    }
  }
</script>

<style lang="stylus">

#location1 {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
     
  

</style>


