<template>
	 <v-layout row wrap>
       
         <v-flex xs12>
	     <header>
			
			  <h1 class="logo">GK</h1>
			  
			  <nav class="site-nav" :class="{'site-nav--open' : this.isok}">
			      <ul>
			        <li><a href=""> <v-icon dark>home</v-icon> Home</a></li>
			        <li><a href=""> <v-icon dark>contacts</v-icon> Contact</a></li>
			        <li><a href=""> <v-icon dark>money</v-icon> Pricing</a></li>
			        <li><a href=""> <v-icon dark>tv</v-icon> Video</a></li>
			        <li><a href=""> <v-icon dark>android</v-icon> App</a></li>
			      </ul> 
			  </nav>
			  
			  <div class="menu-toggle" :class="{'open' : this.isok}" @click='isok = !isok'>
			    <div  class="hamburger"></div>
			  </div>

		  </header>
		</v-flex>
        
        <v-container fluid>
        <v-flex xs12 sm8 md6 offset-sm2 offset-md3>
		<add-gmaps></add-gmaps>
		</v-flex>
		</v-container>
	      
    </v-layout>

</template>



<script>
import Maps from './Maps.vue';
export default {
  components: {
    'add-gmaps': Maps
  }, 
  data() {
    return {
    	isok : false
      
    } 
  }
}
  
</script>




<style>


@import url('https://fonts.googleapis.com/css?family=Quicksand:400,700');

body {
  background: #F0F8EA;
  font-family: 'Quicksand', sans-serif;
}

header {
  background: #E54B4B;
  color: #EBEBD3;
  padding: 1em 2em;
  position: relative;
}

header::after {
  content: '';
  clear: both;
  display: block;
}

.logo {
  float: left;
  font-size: 1rem;
  margin: 0;
  text-transform: uppercase;
  font-weight: 700;
}

.logo span {
  font-weight: 400;
}

.site-nav {
  position: absolute;
  top: 100%;
  right: 0%;
  background: #464655;
  clip-path: circle(0px at top right);
  transition: clip-path ease-in-out 700ms;
  z-index: 10;
/*   display: none; */
}

.site-nav--open {
  clip-path: circle(250% at top right);
  z-index: 10;
/*   display: block; */
}

.site-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-nav li {
  border-bottom: 1px solid #575766;
}

.site-nav li:last-child {
  border-bottom: none;
}

.site-nav a {
  color: #EBEBD3;
  display: block;
  padding: 2em 4em 2em 1.5em;
  text-transform: uppercase;
  text-decoration: none;
}

.site-nav a:hover,
.site-nav a:focus {
  background: #E4B363;
  color: #464655;
}

.site-nav--icon {
  display: inline-block;
  font-size: 1.5em;
  margin-right: 1em;
  width: 1.1em;
  text-align: right;
  color: rgba(255,255,255,.4);
}

.menu-toggle {
  padding: 1em;
  position: absolute;
  top: .5em;
  right: .5em;
  cursor: pointer;
}

.hamburger,
.hamburger::before,
.hamburger::after {
  content: '';
  display: block;
  background: #EBEBD3;
  height: 3px;
  width: 1.75em;
  border-radius: 3px;
  transition: all ease-in-out 500ms;
}

.hamburger::before {
  transform: translateY(-6px);
}

.hamburger::after {
  transform: translateY(3px);
}

.open .hamburger {
  transform: rotate(45deg);
}

.open .hamburger::before {
  opacity: 0;
}

.open .hamburger::after {
  transform: translateY(-3px) rotate(-90deg);
}

@media (min-width: 700px) {
  
  .menu-toggle {
    display: none;
  }
  
  .site-nav {
    height: auto;
    position: relative;
    background: transparent;
    float: right;
    clip-path: initial;

  }
  
  .site-nav li {
    display: inline-block;
    border: none;
  }
  
  .site-nav a {
    padding: 0;
    margin-left: 3em;
  }
  
  .site-nav a:hover,
  .site-nav a:focus {
    background: transparent;
  }
  
  .site-nav--icon {
    display: none;
  }
  
}

</style>



